<template>
    <b-modal :visible="null !== card" v-if="null !== card" @hidden="$emit('hidden')" hide-header hide-footer>
        <div class="mb-3">
            <a href="#" class="btn btn-outline-danger btn-sm float-right" @click="card.deleted = true">Delete</a>
            #{{ card.id }} Created <strong>{{ card.createdAt|moment('DD MMM HH:mm') }}</strong>
        </div>
        <form>
            <div class="form-group">
                <input class="form-control" placeholder="Card title" v-model="card.title">
            </div>
            <div class="form-group">
                <textarea class="form-control" id="exampleFormControlTextarea1" rows="5" v-model="card.description"></textarea>
            </div>

            <div class="checklist-container">
                <a @click.prevent="addChecklistItem" href="#" class="float-right">&xoplus; Add</a>
                <h5>Checklist</h5>
                <table class="checklist-table">
                    <tr v-for="item in card.checklist">
                        <td class="checkbox"><input type="checkbox" v-model="item.checked"></td>
                        <td><input v-model="item.title" class="text-like" placeholder="Task description"></td>
                    </tr>
                </table>
            </div>

            <div class="form-group">
                <multiselect v-model="card.users" :options="board.users" :multiple="true" :close-on-select="false" placeholder="Assigned users" label="name" track-by="id"/>
            </div>

            <div class="btn-group" role="group" aria-label="Basic example" style="width: 100%;">
                <button @click="card.toggleLabel(label.key)" v-for="label in labels" type="button" class="btn btn-sm"
                        :class="`btn-${card.labels.includes(label.key) ? '' : 'outline-'}${label.key}`">{{ label.title }}
                </button>
            </div>
        </form>
    </b-modal>
</template>
<script>

  import {Board, Card, ChecklistItem} from "@/app/model/models";

  export default {
    name: 'EditCardModal',
    props: {
      card: {
        type: Card,
        required: false,
      },
      board: {
        type: Board,
        required: true,
      },
    },
    computed: {
      labels() {
        return [
          {key: 'primary', title: "Primary"},
          {key: 'secondary', title: "Secondary"},
          {key: 'success', title: "Success"},
          {key: 'danger', title: "Danger"},
          {key: 'warning', title: "Warning"},
          {key: 'info', title: "Info"},
        ];
      },
    },
    methods: {
      addChecklistItem() {
        this.card.checklist.push(new ChecklistItem());
      },
    },
  }
</script>

<style scoped>
    .checklist-table {
        width: 100%;
    }

    .checklist-table tr td.checkbox {
        width: 25px;
    }

    .checklist-container {
        border-bottom: 1px solid #dadada;
        margin-bottom: 15px;
        padding-bottom: 5px;
    }

    input.text-like {
        width: 100%;
        border: none;
        background: transparent;
    }

    .user-edit-modal .card-user img {
        height: 19px;
        border-radius: 50%;
    }
</style>


