<template>
    <div class="card">
        <div class="card-labels">
            <div v-for="label in card.labels" class="label" :class="`btn-${label}`"></div>
        </div>

        <div class="card-body">
            <h5 class="card-title">
                <a href="#" @click.prevent="$emit('edit-card', card)">{{ card.title }}</a>
            </h5>
            <p class="card-text">
                {{ card.description }}
            </p>
        </div>

        <div class="card-footer">
            {{ card.createdAt|moment('DD MMM HH:mm') }}

            <div class="users">
                <template v-for="user in card.users">
                    <span v-if="!user.avatar" :key="user.id" class="badge badge-info mr-1" :title="user.name">
                        {{ user.initials }}
                    </span>

                    <img v-else :src="user.avatar" :key="user.id" :title="user.name"/>
                </template>
            </div>
        </div>
    </div>
</template>
<script>
  import {Card} from "@/app/model/models";

  export default {
    name: 'CardComponent',
    props: {
      card: {
        type: Card,
        required: true,
      }
    }
  }
</script>

<style scoped>

    .card {
        margin-bottom: 5px;
    }

    .card-body {
        padding: 3px 0.7rem;
        font-size: 0.7rem;
    }

    .card-footer {
        background: inherit;
        border-top: none;
    }

    .card h5 {
        margin-bottom: 0;
        font-size: 1rem;
    }

    .card h5 a {
        color: inherit;
        width: 100%;
    }

    .card-footer {
        padding: 2px 0.7rem;
        font-size: 0.7rem;
    }

    .card-footer .users {
        float: right;
    }

    .card-footer .users img {
        border-radius: 50%;
        width: 18px;
    }

    .card-labels {
        padding: 4px 0.7rem 0;
    }

    .card-labels div.label {
        width: 30px;
        height: 5px;
        display: inline-block;
        float: left;
        border-radius: 5px;
        margin-right: 5px;
    }

</style>


