import _ from 'lodash';

export class User {
  constructor({id = null, name = null, avatar = null, password = null} = {}) {
    this.id = id;
    this.name = name;
    this.avatar = avatar;
    this.password = password;
  }

  get initials() {
    return this.name.split(' ').map(part => part.substr(0, 1).toUpperCase()).join('');
  }
}

export class Board {
  constructor({id = null, columns = [], users = []} = {}) {
    this.id = id;
    this.columns = columns;
    this.users = users;
  }

  get activeColumns() {
    return this.columns.filter(column => !column.deleted);
  }
}

export class Column {
  constructor({id = null, title = null, cards = []} = {}) {
    this.id = id;
    this.title = title;
    this.cards = cards;

    this.deleted = false;
  }

  searchCards(search) {
    if (!search) {
      return this.cards;
    }

    return this.cards.filter(card => card.match(search));
  }
}

export class Card {
  constructor({id = '', title = '', description = '', createdAt = new Date(), labels = [], users = []} = {}) {
    this.id = id;
    this.title = title;
    this.description = description;
    this.createdAt = createdAt;
    this.labels = labels;
    this.users = users;

    this.checklist = [];

    this.deleted = false;
  }

  match(search) {
    return this.title.includes(search) || this.description.includes(search);
  }

  toggleLabel(label) {
    this.labels = _.xor(this.labels, [label]);
  }

  deleteUser(user) {
    this.users = _.xor(this.users, [user]);
  }

  addUser(user) {
    if (!this.users.includes(user)) {
      this.users.push(user);
    }
  }
}


export class ChecklistItem {
  constructor({checked, title} = {}) {
    this.checked = checked;
    this.title = title;
  }
}


