<template>
    <div id="app">
        <draggable :list="board.columns" group="columns" class="board" :options="{draggable: '.draggable-column'}">
            <div class="column-wrapper draggable-column" v-for="column in board.activeColumns" :key="column.id">
                <div class="column">
                    <div class="column-header">
                        <div class="input-group input-group-sm">
                            <input type="text" class="form-control" v-model="column.title">

                            <div class="input-group-append">
                                <b-dropdown size="sm">
                                    <b-dropdown-item @click="deleteColumn(column)">Delete</b-dropdown-item>
                                </b-dropdown>
                            </div>
                        </div>
                    </div>

                    <draggable :list="column.cards" group="cards">
                        <CardComponent v-for="card in column.searchCards(search)" :key="card.id" v-if="!card.deleted" :card="card" @edit-card="editCard = $event"/>
                    </draggable>

                    <div class="card card-create" v-if="addCardColumnId === column.id">
                        <textarea class="card-title" placeholder="Enter card`s title" v-model="newTitle" @keypress.enter.prevent="createCard(column)" @keyup.esc="addCardColumnId = null"></textarea>

                        <input @click="createCard(column)" type="submit" class="btn btn-primary btn-block" value="Create"/>
                    </div>

                    <div class="column-footer">
                        <input @click="addCardColumnId = column.id" type="button" class="btn btn-secondary btn-sm" value="Add card"/>
                    </div>
                </div>
            </div>

            <div class="column-wrapper">
                <div class="column">
                    <input type="button" value="Create column" class="btn btn-block btn-sm btn-primary" @click="createColumn"/>
                </div>
            </div>
        </draggable>


        <EditCardModal :card="editCard" :board="board" @hidden="editCard = null"/>

    </div>
</template>

<script>
  import {Board, Card, Column} from "@/app/model/models";
  import draggable from 'vuedraggable';
  import EditCardModal from "@/components/EditCardModal";
  import CardComponent from "@/components/CardComponent";

  export default {
    name: "BoardComponent",
    components: {CardComponent, EditCardModal, draggable},
    props: {
      board: {
        type: Board,
        required: true,
      },
      idGenerate: {
        type: Function,
        required: true,
      },
      search: {
        type: String,
        required: false,
      }
    },
    data() {
      return {
        newTitle: null,
        editCard: null,
        addCardColumnId: null,
      };
    },
    methods: {
      createCard(column) {
        column.cards.push(new Card({id: this.idGenerate(), title: this.newTitle}));

        this.newTitle = '';
      },
      createColumn() {
        this.board.columns.push(new Column({id: this.idGenerate(), title: ''}));
      },
      deleteColumn(column) {
        if (!confirm('Are you sure?')) {
          return;
        }

        column.deleted = true;
      },
    },

  }
</script>

<style scoped>
    body {
        padding-top: 60px;
    }

    .board {
        position: absolute;
        top: 60px;
        left: 0;
        right: 0;
        bottom: 0;
        white-space: nowrap;
        overflow-x: scroll;
    }

    .board .column-wrapper {
        height: 100%;
        width: 250px;
        display: inline-block;
        margin-right: 7px;
        vertical-align: top;
    }

    .board .column {
        background-color: #eee;
        padding: 7px;
        border: 1px solid #AAA;
        border-radius: 3px;
    }

    .board .column .column-header {
        margin-bottom: 5px;
    }

    .board .column .column-header button.dropdown-toggle {
        border: none;
    }

    .board .column .column-header input {
        background: transparent;
        border: none;
    }

    .board .column .column-header input:focus {
        background: #FFF;
    }

    .card-create {
        padding: 5px;
    }

    .card-create .card-title {
        margin-bottom: 5px;
        border: none;
        background: transparent;
        resize: none;
    }

    .card-create .card-title:focus {
        box-shadow: none;
        border: none;
        -webkit-appearance: none;
    }

    .card.sortable-ghost {
        background-color: #AAAAAA;
        color: #AAAAAA;
    }

</style>



