<template>
  <div id="app">
    <nav class="navbar navbar-expand-md navbar-dark bg-dark fixed-top">
      <a class="navbar-brand" href="#">Kanban board</a>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarsExampleDefault" aria-controls="navbarsExampleDefault" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarsExampleDefault">
        <ul class="navbar-nav mr-auto">
          <li class="nav-item dropdown">


            <a class="nav-link dropdown-toggle" href="#" id="dropdown01" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Boards</a>
            <div class="dropdown-menu" aria-labelledby="dropdown01">
              <a class="dropdown-item" href="#">Vue demo</a>
              <a class="dropdown-item" href="#">Another</a>
              <a class="dropdown-item" href="#">Something else here</a>
            </div>
          </li>
        </ul>

        <div class="form-inline my-2 my-lg-0">
          <ul class="navbar-nav mr-3">
            <li class="nav-item dropdown">

              <b-dropdown size="sm" class="input-group-append" text="Users">
                <b-dropdown-item v-for="user in board.users" :key="user.id">#{{ user.id }}: {{ user.name }}</b-dropdown-item>
                <b-dropdown-divider/>
                <b-dropdown-item @click="showAddUserForm">Add user</b-dropdown-item>
              </b-dropdown>

            </li>
          </ul>

          <input v-model="search" class="form-control mr-sm-2" placeholder="Search" aria-label="Search">
        </div>
      </div>
    </nav>

    <main role="main" class="container-fluid" style="margin-top: 60px;">
      <BoardComponent :board="board" :id-generate="idGenerate" :search="search"/>
    </main>

    <b-modal :visible="null !== editUser" v-if="null !== editUser" @hidden="editUser = null" hide-footer hide-header>
      <form class="px-4 py-3">
        <div class="form-group">
          <label for="exampleDropdownFormName">Name</label>
          <input v-model="editUser.name" type="text" class="form-control" id="exampleDropdownFormName" placeholder="Ivan Ivanov">
        </div>
        <div class="form-group">
          <label for="exampleDropdownFormAvatar">Avatar</label>

          <input type="file" @change="readImage($event);" class="form-control" id="exampleDropdownFormAvatar" style="width: 100%;">
        </div>
        <div class="form-group">
          <label for="exampleDropdownFormPassword1">Password</label>
          <input v-model="editUser.password" type="password" class="form-control" id="exampleDropdownFormPassword1" placeholder="Password">
        </div>

        <button type="submit" class="btn btn-primary btn-sm btn-block mt-2" @click="createUser">Add user</button>
      </form>
    </b-modal>
  </div>
</template>

<script>

import BoardComponent from "@/components/BoardComponent";
import {Board, Card, Column, User} from "@/app/model/models";



let idCounter = 1;
const idGenerate = () => ++idCounter;

export default {
  name: 'App',
  components: {BoardComponent},
  data() {
    return {
      board: new Board(),
      search: null,
      editUser: null,
    }
  },
  methods: {
    idGenerate(){ return idGenerate();},
    showAddUserForm() {
      this.editUser = new User();
    },
    readImage(event) {
      const reader = new FileReader();

      reader.onloadend = () => {
        this.editUser.avatar = reader.result;
      };

      const file = event.target.files[0];

      if (file) {
        reader.readAsDataURL(file);
      } else {
        alert('Error avatar loading');
      }
    },
    createUser() {
      this.editUser.id = idGenerate();
      this.board.users.push(this.editUser);

      this.editUser = null;
    },
  },
  mounted() {
    this.board.users.push(new User({id: idGenerate(), name: 'Ivan Ivanov'}));
    this.board.users.push(new User({id: idGenerate(), name: 'Pyotr Petrov'}));

    this.board.columns.push(new Column({id: idGenerate(), title: 'TODO'}));
    this.board.columns.push(new Column({id: idGenerate(), title: 'WIP'}));
    this.board.columns.push(new Column({id: idGenerate(), title: 'Done'}));

    this.board.columns[0].cards.push(new Card({id: idGenerate(), title: 'Testing card', description: 'Testing description'}));
  }
}
</script>

<style>
</style>



